.popup__edit-title {
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    margin: 0;
    margin-bottom: 50px;
}

@media screen and (max-width: 768px) {
    .popup__edit-title {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 75px;
    }
}