.profile__name {
    color: #FFFFFF;
    font-family: 'Inter', Arial, Helvetica, sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 340px;
}

@media screen and (max-width: 768px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        padding-left: 34px;
        max-width: 220px;
    }
}