.popup {
    background-color: rgb(0, 0, 0, .5);
    min-height: 100vh;
    width: 100%;
    position: fixed;
    left: 0%;
    top: 0%;
    justify-content: center;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: .3s;
    display: flex;
    cursor: pointer;
}