.profile {
    max-width: 880px;
    margin: 0 auto;
    padding-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media screen and (max-width: 1024px) {
    .profile {
        padding: 40px 30px 0 30px;
    }
}

@media screen and (max-width: 768px) {
    .profile {
        flex-direction: column;
        padding: 42px 19px 0 19px;
    }
}